export default {
	data() {
		return {
			navList: [],
			active: 0,
			itemList: []
		}
	},
	methods: {
		async getPicNavList() {
			const res = await this.$myRequest({
				url: '/shop/getPicNavList'
			})
			this.navList = res.data.result
			this.getNavItem(1)
		},
		navClick(index,id) {
			this.active = index
			this.getNavItem(id)
		},
		async getNavItem(id) {
			const res = await this.$myRequest({
				url: '/shop/getNavDetail?id='+id
			})
			this.itemList = res.data.result
		},
		preView(current){
			const urls = this.itemList.map(item=>{
				return item.imgUrl
			})
			uni.previewImage({
				current,
				urls
			})
		}
	},
	onLoad() {
		this.getPicNavList();
	}
}
